@charset "UTF-8";
@font-face {
  font-family: 'TTLakes-Regular';
  src: url(./fonts/TTLakes-Regular.woff);
  font-style: normal; }

@font-face {
  font-family: 'TTLakes-Medium';
  src: url(./fonts/TTLakes-Medium.woff);
  font-style: normal; }

@font-face {
  font-family: 'TTLakes-Bold';
  src: url(./fonts/TTLakes-Bold.woff);
  font-style: normal; }

.swiper__header {
  height: 80px;
  padding: 16px;
  background: linear-gradient(to right, #F8F8F8, #FFFFFF); }

.swiper__head {
  font-family: 'TTLakes-regular';
  font-weight: 400;
  font-style: normal;
  color: #7E7E82;
  margin: 0;
  font-size: 16px;
  text-transform: uppercase; }

.swiper__block {
  background-color: #F8F8F8;
  min-width: 320px;
  border-top: 2px solid #D9FFF5; }

.swiper-container {
  width: 100%; }

.more {
  display: flex;
  text-decoration: none; }
  .more__text {
    margin-left: 16px;
    color: black;
    font-family: 'TTLakes-Medium'; }
  .more__container {
    margin: 16px 0; }

.more__container {
  display: inline-block; }

.more__img--rotate {
  display: inline-block;
  transform: rotate(180deg); }

.repair__more {
  display: none;
  margin-left: 24px; }

.repair__container {
  padding: 24px; }

.repair-slide {
  width: 192px;
  height: 72px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding: 0 15px; }

.preview__list {
  margin-left: 16px; }

.preview__list {
  display: flex;
  list-style-type: none;
  padding-left: 0;
  white-space: nowrap;
  overflow: hidden; }

.preview__link {
  text-decoration: none;
  color: #7E7E82;
  font-family: 'TTLakes-Medium';
  padding: 8px 16px;
  display: block; }

.preview__item:first-child .preview__link {
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background-color: #FFFFFF; }

.preview__text {
  height: 90px;
  overflow: hidden; }

.preview__paragraph {
  font-size: 14px;
  font-family: 'TTLakes-Regular'; }

.preview__logo {
  min-width: 320px; }

.preview__photo {
  width: 100%; }

.header__container {
  background-color: #FFFFFF;
  border-bottom: 1px solid #D9FFF5; }

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.tech__more {
  display: none;
  margin-left: 24px; }

.tech__container {
  padding: 24px; }

.tech-slide {
  width: 192px;
  height: 128px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding: 15px 15px;
  flex-direction: column; }

.tech-link {
  align-self: flex-end; }

.tech__span {
  font-family: 'TTLakes-Medium';
  align-self: flex-start; }

.price__more {
  display: none;
  margin-left: 24px; }

.price__container {
  padding: 24px; }

.price-slide {
  width: 192px;
  height: 128px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding: 16px;
  flex-direction: column; }

.price-link {
  align-self: flex-end; }

.price__span {
  font-family: 'TTLakes-Regular';
  align-self: flex-start;
  font-size: 14px;
  margin-right: 28px; }

.price-slide__button {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.price__paragraph {
  font-family: 'TTLakes-Regular';
  font-size: 14px; }

.price__link-advice {
  display: block;
  text-decoration: none;
  color: black;
  font-family: 'TTLakes-Medium'; }

.footer__container {
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 19px;
  border-top: 1px solid #D9FFF5; }

.footer__container-block {
  display: flex;
  flex-direction: column;
  align-items: center; }

.footer__block {
  margin: 0;
  margin-top: 16px;
  color: #7E7E82;
  font-size: 14px;
  font-family: 'TTLakes-Regular';
  text-align: center;
  white-space: pre-line; }

.nav-bar__container {
  width: 320px;
  height: 100%;
  border: 1px solid white;
  box-shadow: 21px 3px 49px -9px rgba(0, 0, 0, 0.71);
  overflow-y: scroll; }

.nav-bar__header {
  width: 320px; }

.nav-bar__list {
  list-style: none;
  padding: 0;
  margin: 18px 24px 50px; }

.nav-bar__header-nav {
  display: flex;
  justify-content: space-around;
  align-items: center; }

.nav-bar__header-nav li:nth-child(2) {
  margin-right: auto; }

.nav-bar__header-nav li:nth-child(1) {
  margin-right: 16px; }

.nav-bar__header-nav a {
  display: block; }

/* Главная менюшка */
.nav-bar__main a {
  display: block;
  text-decoration: none;
  color: #1B1C21; }

.nav-bar__footer-social a {
  display: block; }

.nav-bar__main ul {
  margin: 0;
  padding: 0;
  padding-left: 24px;
  margin-bottom: 30px; }

.nav-bar__main li {
  margin-bottom: 32px; }

.nav-bar__main li:first-child::before {
  content: url(./static/highlight.svg);
  position: absolute;
  left: 0; }

.nav-bar__main li:first-child {
  display: flex;
  flex-direction: row;
  align-items: center; }

.nav-bar__main li:first-child a {
  color: #7E7E82; }

/* Футер */
.nav-bar__footer-social {
  padding: 0;
  margin: 0;
  padding-left: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.nav-bar__footer-social li:not(:nth-child(3)) {
  margin-right: 16px; }

.nav-bar__footer-mail {
  margin-left: 24px;
  margin-top: 16px;
  margin-bottom: 12px; }

.nav-bar__footer-contact-number {
  margin-left: 24px;
  font-size: 24px;
  margin-bottom: 24px; }

.nav-bar__footer-language {
  padding: 0;
  margin: 0;
  margin-left: 24px;
  margin-bottom: 32px;
  display: flex; }

.nav-bar__footer-language a {
  font-size: 16px;
  color: #1B1C21;
  text-decoration: none; }

.nav-bar__footer-language li {
  margin-right: 7px; }

.nav-bar__footer-language li:first-child a {
  color: #7E7E82; }

.nav-bar__button:hover {
  transition: 3s;
  transform: scale(1.3); }

.nav-bar {
  opacity: 0;
  visibility: hidden;
  z-index: 11;
  position: fixed;
  background-color: white;
  width: 320px;
  height: 100%;
  transform: translate(-350px);
  transition: 1s;
  transition-property: transform;
  font-family: 'TTLakes-Regular'; }

.nav-bar--active {
  opacity: 1;
  visibility: visible;
  transform: translate(0px); }

address {
  font-style: normal; }

.all-wrap {
  position: relative; }

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.712);
  top: 0;
  left: 0;
  z-index: 11;
  opacity: 0;
  visibility: hidden;
  display: block; }

.popup__blur {
  width: 100%;
  height: 100%; }

.popup__body {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-end; }

.popup__content {
  min-width: 320px;
  max-width: 440px;
  background-color: white;
  font-family: 'TTLakes-Regular';
  position: relative;
  padding: 24px 16px;
  transform: translate(500px);
  transition-property: transform;
  transition: 1s;
  overflow-y: scroll;
  max-height: 100%;
  height: 100%; }

.popup__input {
  box-sizing: content-box;
  height: 24px;
  width: 240px;
  border-radius: 8px;
  border: 1px solid #EAEAEA;
  padding: 12px; }

.popup__item {
  margin-bottom: 16px; }

.popup__title {
  font-size: 24px;
  font-family: 'TTLakes-Bold';
  margin-top: 30px;
  margin-bottom: 24px; }

.popup-button__item {
  font-family: TTLakes, sans-serif;
  color: white;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  font-weight: bold;
  width: 126px;
  height: 32px;
  border-radius: 15px;
  border: 0;
  padding: 4px 34px 4px 4px;
  background: no-repeat 105px center url(./static/dropdown.svg) #FF3E79;
  cursor: pointer; }

.popup-button__container {
  display: flex;
  justify-content: flex-end; }

.button {
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  border: 0;
  border-radius: 20px;
  background-repeat: no-repeat;
  background-position: 50% 50%; }

.button-burger {
  background-image: url(./static/icon7.svg); }

.button-call {
  background-image: url(./static/icon3.svg); }

.button-chat {
  background-image: url(./static/icon5.svg); }

.button-user {
  background-image: url(./static/icon6.svg); }

.button-repair {
  background-image: url(./static/icon8.svg); }

.button-search {
  background-image: url(./static/icon9.svg); }

.button-close {
  background-image: url(./static/icon1.svg); }

.button-search-2 {
  background-image: url(./static/icon2.svg); }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  background-color: #F8F8F8; }

.body--hidden {
  overflow: hidden; }

.container {
  margin: 0 16px; }

.nav {
  display: flex;
  list-style-type: none;
  padding-left: 0; }

.item-container {
  padding-left: 16px; }

.nav--hidden {
  display: none; }

.border--hidden {
  display: none; }

.head {
  font-size: 28px;
  font-family: 'TTLakes-Bold';
  position: relative;
  margin: 24px 0; }

.head::before {
  content: url(./static/highlight.svg);
  position: absolute;
  left: -17px;
  top: -5px; }

.tools--hidden {
  display: none; }

.chat-active {
  opacity: 1;
  visibility: visible; }

.chat-active .popup__content {
  transform: translate(0px); }

.repair-slide {
  width: 208px; }

.tech-slide {
  width: 208px; }

.preview__list {
  overflow: scroll; }

@media (min-width: 768px) {
  .nav--hidden {
    display: flex; }
  .nav-bar__main ul {
    margin-bottom: 268px; }
  .border--hidden {
    display: flex; }
  .logo {
    margin-right: auto; }
  .nav:nth-of-type(1) {
    margin-right: 20px; }
  .border:nth-of-type(1) {
    margin-right: 20px; }
  .border:nth-of-type(3) {
    margin-left: 20px; }
  .nav__second {
    margin-left: 16px; }
  .container {
    margin: 0 24px; }
  .head::before {
    content: url(./static/highlight.svg);
    position: absolute;
    left: -25px;
    top: -5px; }
  .welcome__container {
    display: flex;
    justify-content: space-between; }
  .preview__logo {
    min-width: 165px;
    margin-right: 16px; }
  .preview__photo {
    border-radius: 6px; }
  .preview__text {
    height: 155px;
    width: 360px;
    overflow: hidden; }
  .repair__wrapper, .tech__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 768px;
    margin: 0 auto; }
  .repair-slide {
    width: 192px;
    margin-left: 24px; }
  .tech-slide {
    width: 192px;
    margin-left: 24px; }
  .swiper-slide {
    margin-bottom: 16px; }
  .repair__container, .tech__container {
    padding: 24px 0; }
  .price__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    background-color: #FFFFFF;
    box-sizing: border-box; }
  .price__container {
    max-width: 1056px; }
  .price-slide {
    flex-direction: row;
    margin: 0;
    border: 0;
    border-bottom: 1px solid #EAEAEA;
    width: 100%;
    height: 64px; }
  .price-span-container {
    display: flex;
    width: 100%;
    max-width: 550px;
    justify-content: space-between; }
  .popup__input {
    width: 366px; }
  .price__wrapper {
    display: grid;
    grid-template-columns: 100%; }
  .price__span {
    min-width: 80px; }
  .price-slide__button .price__span {
    height: 36px;
    display: flex;
    align-items: center; }
  .price-slide__button {
    display: grid;
    grid-template-columns: minmax(80px, 200px) 120px; }
  .footer__container {
    margin: 47px 24px 25px 24px;
    border-bottom: 1px solid white; }
  .footer__container-block {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between; }
  .footer__block {
    margin: 0;
    width: 213px; }
  .footer__block:first-child {
    text-align: left;
    width: 218px; }
  .footer__block:last-child {
    text-align: right;
    width: 213px; } }

@media (min-width: 1120px) {
  .head-container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .content {
    max-width: 1120px;
    margin: 0 auto; }
  .container {
    margin: 0 34px; }
  .head::before {
    content: url(./static/highlight.svg);
    position: absolute;
    left: -34px;
    top: -5px; }
  .preview__list {
    overflow: visible;
    flex-wrap: wrap;
    margin: 0 34px; }
  .preview__text {
    overflow: visible; }
  .preview__text {
    min-width: 550px; }
  .welcome__container {
    justify-content: center; }
  .preview__logo {
    width: 100%; }
  .repair__wrapper, .tech__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 1120px;
    margin: 0 auto; }
  .repair__container, .tech__container {
    padding: 24px 0; }
  .repair-slide {
    width: 208px;
    margin-left: 32px; }
  .tech-slide {
    width: 208px;
    margin-left: 32px; } }

@media (min-width: 1440px) {
  .content {
    margin: 0;
    margin-left: auto; }
  .nav-bar__container {
    overflow-y: visible; }
  .all-wrap {
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    position: relative; }
  .tools--hidden {
    display: flex; }
  .tools {
    display: flex; }
  .tools__item {
    display: flex;
    margin-left: 32px;
    align-items: center; }
  .tools__text {
    margin-right: 9px;
    font-family: 'TTLakes-Medium'; }
  .header {
    display: none; }
  .nav-bar__container {
    box-shadow: none;
    border: none; }
  .nav-bar {
    opacity: 1;
    visibility: visible;
    transition: 1s;
    transform: translate(0px);
    position: absolute; }
  .nav-bar__button.button--close {
    display: none; } }
